html, body{
    margin: 0;
    padding: 0;
    font-family: 'Open Sans', sans-serif;
    background-color: #f1f1f1;
    color: #282828;
}

h1 {
    margin: 10px;
    text-align: center;
}

.wrapper {
    display: grid;
    grid-template-columns: 1fr 2fr 1fr;
    grid-template-rows: minmax(30px, auto);
}

.banner {
    grid-column: 1/4;
    text-align: center;
}
