.pie-container {
    position: absolute;
}

.pie-slice-container:hover .pie-slice-bg {
    stroke-width: 0;
    transition: stroke-width 0.5s;
}

.pie-slice-container, .pie-overlay {
    cursor: pointer;
}

.pie-overlay:hover .pie-overlay-bg {
    opacity: 1;
    transition: opacity 0.25s;
}